<template>
  <div class="product">
    <div class="product-container d-flex mb-2">
      <div class="product-container__avatar mr-2">
        <img class="mx-auto" v-if="product.image" :src="product.image"/>
      </div>
      <div class="product-container__information text-center w-100 d-flex flex-column">
        <p class="text-center h6 text-primary">
          На <strong>100</strong> гр. продукту
        </p>
        <div class="information-items d-flex mt-auto">
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">Б</span>
            <span class="information__footer">{{ product.product_b || '0' }}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">Ж</span>
            <span class="information__footer">{{ product.product_j || '0' }}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">У</span>
            <span class="information__footer">{{ product.product_y || '0' }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="product-form">
      <form @submit="setProductPouchDB">
        <div class="mb-3">
          <label for="productName" class="form-label">Название продукту</label>
          <input required v-model="product.title" type="text" class="form-control" id="productName" placeholder="">
        </div>
        <div class="mb-3">
          <label class="form-label">Иснуюча категоря</label>
          <b-form-select @change="onDefault" v-model="product.category_id" class="mb-3">
            <b-form-select-option :value="null">-- Нету ---</b-form-select-option>
            <b-form-select-option :value="items.id" v-for="(items, index) in categories" :key="index">{{ items.doc.title }}</b-form-select-option>
          </b-form-select>
        </div>
        <div class="mb-3">
          <label for="productCategory" class="form-label">Нова категория</label>
          <input :required="!product.category_id" :disabled="product.category_id" v-model="category.title" type="text" class="form-control"
                 id="productCategory" placeholder="">
        </div>
        <div class="row mb-3">
          <div class="col-6">
            <label for="productB" class="form-label">Белков на 100гр.</label>
            <input required v-model="product.product_b" type="number" step="0.1" class="form-control" id="productB" placeholder="">
          </div>
          <div class="col-6">
            <label for="productH" class="form-label">Жиров на 100гр.</label>
            <input required v-model="product.product_j" type="number" step="0.1" class="form-control" id="productH" placeholder="">
          </div>
        </div>
        <div class="mb-3">
          <label for="productY" class="form-label">Углеводов на 100гр.</label>
          <input required v-model="product.product_y" type="number" step="0.1" class="form-control" id="productY" placeholder="">
        </div>
        <div class="mb-3">
          <label for="productAvatar" class="form-label">Картинка</label>
          <b-form-file browse-text="Выбрать" placeholder="Выбрать картинку" @change="onChange" ref="file-input" id="productAvatar" class="mb-2"></b-form-file>
        </div>
        <b-button type="submit" class="btn-lg w-100" variant="primary" :disabled="isCreated">
          <b-spinner style="width: 25px; height: 25px;" type="grow" v-show="isCreated"></b-spinner>
          Редактировать
        </b-button>
        <!--        <button type="submit" class="btn btn-primary btn-lg w-100">Добавить</button>-->
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Edit',
  data() {
    return {
      isSync: false,
      isCreated: false,
      product: {
        title: '',
        category_id: null,
        product_b: '',
        product_j: '',
        product_y: '',
        image: null,
        gram: 100,
        is_home: 0,
        variable_1: '',
        variable_2: '',
        variable_3: '',
        variable_4: '',
        variable_5: '',
      },
      category: {
        title: '',
      },
      products: [],
      categories: [],
    }
  },
  computed: {
    isOnline() {
      return window.navigator.onLine;
    },
    url() {
      return this.$route.params.id
    }
  },
  methods: {
    resizeImage(base64Str, maxWidth = 100, maxHeight = 100) {
      return new Promise((resolve) => {
        let img = new Image()
        img.src = base64Str
        img.onload = () => {
          let canvas = document.createElement('canvas')
          const MAX_WIDTH = maxWidth
          const MAX_HEIGHT = maxHeight
          let width = img.width
          let height = img.height
          //
          if (width > height) {
            if (width > MAX_WIDTH) {
              height *= MAX_WIDTH / width
              width = MAX_WIDTH
            }
          } else {
            if (height > MAX_HEIGHT) {
              width *= MAX_HEIGHT / height
              height = MAX_HEIGHT
            }
          }
          canvas.width = width
          canvas.height = height
          let ctx = canvas.getContext('2d')
          ctx.drawImage(img, 0, 0, width, height)
          resolve(canvas.toDataURL())
        }
      })
    },
    getBase64(file) {
      return new Promise(function (resolve, reject) {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result)
        reader.onerror = (error) => reject('Error: ', error);
      })
    },

    onChange(event) {
      const self = this;
      this.getBase64(event.target.files[0]).then(images => {
        self.resizeImage(images).then(img => {
          self.product.image = img;
        })
      })
    },
    onDefault() {
      this.category.title = '';
    },
    reset() {
      this.category.title = '';
      this.product.title = '';
      this.product.category_id = null;
      this.product.product_b = '';
      this.product.product_j = '';
      this.product.product_y = '';
      this.product.image = null;
    },

    //Get
    async getCategories() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        },'category').then((res) => {
          this.categories = res.rows;
        });
      } catch (error) {
        console.error(error)
      }
    },

    async getProducts() {
      try {
        await this.$pouch.get(this.url).then((res) => {
          this.product = res;
        });
      } catch (error) {
        console.error(error)
      }
    },

    //set DB
    async setCategoryPouchFB(id) {
      try {
        await this.$firebase.database().ref().child(`category/${id}`).update({
          _id: id,
          ...this.category
        });
      } catch (error) {
        console.error(error)
      }
    },

    async setProductPouchFB(id) {
      try {
        await this.$firebase.database().ref().child(`product/${id}`).update({
          _id: id,
          ...this.product
        })
      } catch (error) {
        console.error(error)
      }
    },

    async setProductPouchDB(evt) {
      evt.preventDefault();
      this.isCreated = true;
      this.product.gram = 100;
      try {
        if (this.category.title) {
          await this.$pouch.post({
            ...this.category
          }, {}, 'category').then((res) => {
            this.product.category_id = res.id;
            if (this.isOnline) {
              this.setCategoryPouchFB(res.id)
            }
          });
        }
        await this.$pouch.post({
          ...this.product
        }).then((res) => {
          if (this.isOnline) {
            this.setProductPouchFB(res.id).then(() => {
              this.reset();
              this.isCreated = false;
              this.$router.back()
            })
          } else {
            this.reset();
            this.isCreated = false;
            this.$router.back()
          }
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.getProducts();
    this.getCategories();
  }
}
</script>

<style scoped lang="scss">
.product {
  .product-container {
    &__avatar {
      width: 100px;
      min-width: 100px;
    }

    &__information {
      height: 100px;

      .information {
        margin: 0 2px;

        .information__header {
          border-bottom: 1px solid green;
        }

        .information__footer {
          font-weight: 500;
          font-size: 18px;
        }
      }
    }
  }
}
</style>
